import { Dialog } from 'vant';  
class Usjl{    
    validForm(yz){
        for(let key in yz){
            if(!yz[key].reg.test(yz[key].value)){
                
                Dialog.alert({
                    message: (yz[key].error),
                  }).then(() => {
                    // on close
                  });
                return false;
            }
        }
        return true;
    }
    danchu(){
        Dialog.confirm({
            title: '退出登录',
            message: '是否要退出登录',
          }) .then(() => {
            return this.$router.push({ name: "Login" });
             
          })
          .catch(() => {
            // on cancel
          });   
          return true;   
    }
}
export const usjl= new Usjl();