<template>
  <div class="login">
    <RXin />
    <div class="login-box">
      <div class="login-title-box">
        <div class="login-title">今天想喝点什么呢!</div>
        <div class="login-sub-title">What would you like to drink today!</div>
      </div>

      <div class="form-box">
         <van-field left-icon="contact" v-model="userLoginInfo.phone" label="手机号" placeholder="输入手机号(11位)" />
         <van-field  left-icon="newspaper-o" @click-right-icon='toggle' v-model="userLoginInfo.password" :type="istype ? 'text':'password'" label="密码" placeholder="输入密码(6~16位)" :right-icon="istype ?'eye-o':'closed-eye'"/>

         <div class="forgot">
            <span @click="zhmm" style="color:white">忘记密码？</span>
          </div>

         <div class="btn-box">
           <van-button round block @click="login" color="linear-gradient(to bottom, skyblue 0%, pink 100%)">登&nbsp;&nbsp;&nbsp;录</van-button>
           <van-button @click="isp" class="register-btn" type="default" round block color="linear-gradient(to bottom, skyblue 0%, pink 100%)">注&nbsp;&nbsp;&nbsp;册</van-button>
         </div>
      </div>
        <van-popup v-model="isopen" closeable
          close-icon="close" round position="bottom">
          <div class="zchz">
            <div class="zc">注册</div>
            <div class="form-box">
         <van-field left-icon="contact" v-model="userRegisterInfo.phone" label="手机号" placeholder="输入手机号(11位)" />
         <van-field  left-icon="newspaper-o" v-model="userRegisterInfo.password" @click-right-icon='toggle' :type="istype ? 'text':'password'" label="密码" placeholder="输入密码(6~16位)" :right-icon="istype ?'eye-o':'closed-eye'" />
         <van-field left-icon="friends-o" v-model="userRegisterInfo.nickName" label="昵称" placeholder="输入昵称(1~10位)" />

         <div class="btn-box">
           <van-button round block color="linear-gradient(to bottom, skyblue 0%, pink 100%)" @click="close">确&nbsp;&nbsp;&nbsp;定</van-button>
         </div>
      </div>
          </div>
        </van-popup>
    </div>
  </div>
</template>

<script>
import "../assets/less/login.less";

import RXin from '../components/RXin.vue';

import {usjl} from '../assets/js/usjl.js';

export default {
  name: "Login",

  data() {
    return {
      userLoginInfo: {
        phone: '',
        password: '',
      },
      userRegisterInfo: {
        phone: '',
        password: '',
        nickName:'',
      },
      isopen:false,
      istype:false,
    };
  },

  components: {
    RXin
  },
  methods: {
    isp(){
      this.isopen=true
    },

    //注册账号
    close(){
      let yz = {
        phone:{
          value:this.userRegisterInfo.phone,
          reg:/^1[3-9]\d{9}$/,
          error:'手机号码格式不正确'
        },
        password:{
          value:this.userRegisterInfo.password,
          reg:/^[A-Za-z]\w{5,15}$/,
          error:'密码支持字母数字_组合首字符为字母'
        },
        nickName:{
          value:this.userRegisterInfo.nickName,
          reg:/^[\u4e00-\u9fa5A-Za-z0-9]{1,10}$/,
          error:'昵称支持汉字字母数字组合'
        },
      };
      // this.isopen=false
      if(!usjl.validForm(yz)){
          return;
      }

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method:"POST",
        url:this.baseUrl + '/register',

        data:{
          appkey:'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
          nickName:this.userRegisterInfo.nickName,
          password:this.userRegisterInfo.password,
          phone:this.userRegisterInfo.phone
        }
      }).then(res =>{
        
        this.$toast.clear()
        // 
        if(res.data.code == 100){
          this.isopen=false;
          for(let key in this.userRegisterInfo){
            this.userRegisterInfo[key] = '';
          }
        }else {
          this.$toast({
            message:res.data.msg,
            forbidClick: true,
            duration: 3000
          })
        }
      }).catch(err =>{
        this.$toast.clear()
        
      })
    },

    //登录用户
    login(){
      let yz = {
        phone:{
          value:this.userLoginInfo.phone,
          reg:/^1[3-9]\d{9}$/,
          error:'手机号码格式不正确'
        },
        password:{
          value:this.userLoginInfo.password,
          reg:/^[A-Za-z]\w{5,15}$/,
          error:'密码支持字母数字_组合首字符为字母'
        },
      };

      if(!usjl.validForm(yz)){
          return;
      }

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      });

      

      this.axios({
        method:"POST",
        url:this.baseUrl + '/login',

        data:{
          appkey:'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
          password:this.userLoginInfo.password,
          phone:this.userLoginInfo.phone
        }
      }).then(res =>{
        
        this.$toast.clear()

        if(res.data.code === 200){
          //保存token
          localStorage.setItem("tokenString",res.data.token,'1d');  

          //去到其他页面
          this.$router.push({name:'Honm'});
        }else{
           this.$toast({
            message:res.data.msg,
            forbidClick: true,
            duration: 3000
          })
        }
      }).catch(err =>{
        this.$toast.clear()
        
      })
    },


    toggle(){
      this.istype=!this.istype;
    },

    zhmm(){
      this.$router.push({name:'Zhaohui'});
    }
  }
};
</script>